import React, { lazy, Suspense } from 'react';
import { Routes, Route } from 'react-router-dom';
import { ProgressBar } from "./SubComponents/ProgressBar";
import { Guid } from './Guid';
import { DialogBox } from "./SubComponents/Modal";
import "./Content.css";

const Home = lazy(() => import('./Home'));
const Login = lazy(() => import('./Login'));
const Register = lazy(() => import('./Register'));
const Village = lazy(() => import("./Village"));
const Center = lazy(() => import("./Center"));
const Building = lazy(() => import("./Building"));

/**@param {{ ready: Boolean, authorized: Boolean }} props */
function Content(props) {
    return <>
        <ProgressBar />
        <DialogBox name="guid" title="الدليل السريع">
            <Guid />
        </DialogBox>
        <div className="content">
            <Suspense fallback={<ProgressBar visiable={true} fallback={true} />}>
                <Routes>
                    <Route path={"/"} element={<Home />} />
                    <Route path={"/Home"} element={<Home />} />
                    <Route path="/Login" element={<Login />} />
                    <Route path="/Register" element={<Register />} />
                    <Route path="/Rules" element={"التعليمات"} />
                    {/* Auth */}
                    <Route path="/Village" element={<Village {...props} />} />
                    <Route path="/Center" element={<Center {...props} />} />
                    <Route path="/Map" element={"الخريطة"} />
                    <Route path="/Statistics" element={"الاحصائيات"} />
                    <Route path="/Reports" element={"التقارير"} />
                    <Route path="/Inbox" element={"الرسائل"} />
                    <Route path="/Building" element={<Building {...props} />} />
                    <Route path="/View" element={"عرض القرية"} />
                    <Route path="/Overview" element={"نظرة عامة على القرى"} />
                    <Route element={<><br /><h3 className="NotFound">الصفحة التي طلبتها غير موجودة</h3></>} />
                </Routes>
            </Suspense>
        </div>
    </>
}

export default Content;