import React from "react";
import { Link } from "react-router-dom";
import { Button } from "react-bootstrap";
import Pure from "./Pure";
import { mDialog } from "./SubComponents/Modal";
import Building from "./CLASSES/buildings";
import { Troop, Race } from "./CLASSES/troops";
import ImageMap from "./Images";
import Converter from "./Functions/timeConverter";
// import { TroopsMap, RaceName, RaceTroops, TroopRace, TroopsName } from "./CLASSES/troops";
import "./Guid.css";

const Routes = {
    type: 0,
    display: 0,
    stack: [],
    gid: 0,
    refreshGid: () => { },
    clear() {
        this.type = 0,
            this.display = 0;
        this.stack = [];
    }
};

const Updater = {
    ready: false,
    ui: (type, display) => { },
    clear() {
        this.ready = false;
        this.ui = (type, display) => { };
    }
}

function GuidRouter(type = 0, display = 0) {
    return (e => {
        e.preventDefault();
        Updater.ui(type, Number(display));
    });
}

function OpenGuid(type = 0, display = 0) {
    return (e => {
        e.preventDefault();
        if (type > 0)
            Routes.stack = [[0, 0]];
        Routes.type = type;
        Routes.display = Number(display);
        mDialog.show("guid");
    });
}

class BuildingInfo extends Pure {
    state = { gid: Routes.gid }
    onReady() {
        Routes.refreshGid = () => { this.updateState({ gid: Routes.gid }); };
    }
    onRemoved() {
        Routes.refreshGid = () => { };
    }
    getCosts(building) {
        let costs = building.costs[0];
        return <div key="costs">
            <h5>تكاليف المستوى الأول</h5>
            <label><img src={ImageMap.pixel} className="r1" />{costs[0]}</label>
            <label><img src={ImageMap.pixel} className="r2" />{costs[1]}</label>
            <label><img src={ImageMap.pixel} className="r3" />{costs[2]}</label>
            <label><img src={ImageMap.pixel} className="r4" />{costs[3]}</label>
            <label><img src={ImageMap.pixel} className="r5" />{building.consumption[0]}</label>
            <label><img src={ImageMap.pixel} className="clock" />{Converter.toTime(building.time[0])}</label>
        </div>
    }
    getRaceRequirements(building) {
        let raceRequirements = [];
        let all = building.race.filter(race => race);
        if (all.length !== 5)
            building.race.forEach((r, i) => {
                if (r === true) {
                    if (raceRequirements.length > 0) raceRequirements.push(", ");
                    raceRequirements.push(<Link key={i} to="#" onClick={GuidRouter(1, Race.Ids[i])}>{Race.Names[i]}</Link>);
                }
            });
        return raceRequirements;
    }
    getPreRequiresites(building) {
        let preRequiresites = [];
        building.preRequiresites.forEach((b, i) => {
            if (preRequiresites.length > 0) preRequiresites.push(<br />);
            preRequiresites.push(<label key={i}>{b[1] === -1 ? "عدم وجود " : ""}<Link to="#" onClick={GuidRouter(4, b[0])}>{Building.Name(b[0])}</Link>{b[1] === -1 ? " في القرية" : " المستوى " + b[1]}</label>);
        });
        return preRequiresites;
    }
    requirements(building) {
        let costs = this.getCosts(building);
        let race = this.getRaceRequirements(building);
        let build = this.getPreRequiresites(building);
        let returned = [costs];
        if (race.length > 0 || build.length > 0) {
            returned.push(<h5 key="requirements">المتطلبات</h5>);
            if (race.length > 0) returned.push(<div key="race"><label>القبيلة: {race}</label></div>);
            if (build.length > 0) returned.push(<div key="build">{build}</div>);
        }
        return returned;
    }
    render() {
        let building = Building.New(this.state.gid);
        return <>
            <h4>{building.name}</h4>
            <img src={building.image} />
            <div className="building-desc">{building.description}</div>
            <div className="building-cost">{this.requirements(building)}</div>
        </>;
    }
}

class BuildingsTypesInfo extends Pure {
    type = this.props.type || "1";
    title = ["الموارد", "الجيش", "البنية التحتية"];
    render() {
        let Content = [];
        Building.Type(this.type).forEach((gid, index) => {
            Content.push(<li key={index}><Link to="#" onClick={GuidRouter(4, gid)}>{Building.Name(gid)}</Link></li>)
        });
        return <ul>
            <li><b>المباني ({this.title[Number(this.type) - 1]})</b></li>
            <ul>{Content}</ul>
        </ul>
    }
}

class TroopInfo extends Pure {
    unit = this.props.unit || "1";
    render() {
        let troop = Troop.New(this.unit);
        return <>
            <b>{troop.name}</b>
        </>
    }
}

class RaceInfo extends Pure {
    race = this.props.race || "1";
    render() {
        /** @type {Number[]} */
        let Content = [];
        Race.Troops(this.race).forEach((unit, index) => {
            Content.push(<li key={index}><Link to="#" onClick={GuidRouter(2, unit)}>{Troop.Name(unit)}</Link></li>);
        });
        return <ul>
            <li><b>قبيلة {Race.Name(this.race)}</b></li>
            <ul>{Content}</ul>
        </ul>;
    }
}

class Main extends Pure {
    Race() {
        let race = [];
        Race.Names.forEach((name, index) => {
            race.push(<li key={name + index}><Link to="#" onClick={GuidRouter(1, Race.Ids[index])}>{name}</Link></li>);
        });
        return race;
    }
    render() {
        return <>
            <ul>
                <li>القبائل:</li>
                <ul>{this.Race()}</ul>
            </ul>
            <br />
            <ul>
                <li>المباني:</li>
                <ul>
                    <li><Link to="#" onClick={GuidRouter(3, 1)}>الموارد</Link></li>
                    <li><Link to="#" onClick={GuidRouter(3, 2)}>الجيش</Link></li>
                    <li><Link to="#" onClick={GuidRouter(3, 3)}>البنية التحتية</Link></li>
                </ul>
            </ul>
        </>;
    }
}

class Guid extends Pure {
    state = { update: 0 }
    constructor(props) {
        super(props);
        this.type = Routes.type || 0;
        this.display = Routes.display || 0;
    }
    onReady() {
        Updater.ready = true;
        Updater.ui = (type, display) => {
            if (type > 0)
                Routes.stack.push([this.type, this.display]);
            this.refresh(type, display);
        }
    }
    onRemoved() {
        Routes.clear();
        Updater.clear();
    }
    refresh(type = 0, display = 0) {
        this.type = type;
        this.display = display;
        this.updateState({ update: Date.now() });
    }
    navigate() {
        if (Routes.stack.length > 0) {
            let info = Routes.stack.pop();
            this.refresh(info[0], info[1]);
        }
        else mDialog.hide("guid");
    }
    Data() {
        if (this.type === 0)
            return <Main />;
        else if (this.type === 1)
            return <RaceInfo race={this.display} />;
        else if (this.type === 2)
            return <TroopInfo unit={this.display} />;
        else if (this.type === 3)
            return <BuildingsTypesInfo type={this.display} />;
        else if (this.type === 4) {
            Routes.gid = this.display;
            Routes.refreshGid();
            return <BuildingInfo gid={this.display} />;
        }
    }
    render() {
        return <>
            <div className={this.type === 4 ? "user-guid center-guid" : "user-guid"}>{this.Data()}</div>
            <div className="guid-footer">
                <Button variant="secondary" onClick={() => this.navigate()}>{Routes.stack.length > 0 ? "عودة" : "إغلاق"}</Button>
            </div>
        </>;
    }
}

export { Guid, OpenGuid }