import React from "react";
import Pure from "../Pure";
import { Modal } from "react-bootstrap";
import { Button } from "react-bootstrap";
import "./Modal.css";

const Dialogs = {};

const mDialog = {
  show: (name) => {
    if (typeof Dialogs[name] === "object")
      Dialogs[name].show();
  },
  hide: (name) => {
    if (typeof Dialogs[name] === "object")
      Dialogs[name].hide();
  },
};

class DialogBox extends Pure {
  state = { visiable: this.props.visiable || false };
  footer = this.props.footer || false;
  name = this.props.name || "default";
  onReady() {
    Dialogs[this.name] = {
      show: () => this.show(),
      hide: () => this.hide(),
    };
  }
  onRemoved() {
    Dialogs[this.name].show = () => { };
    Dialogs[this.name].hide = () => { };
  }
  show() {
    this.updateState({ visiable: true });
  }
  hide() {
    this.updateState({ visiable: false });
  }
  render() {
    return (
      <Modal show={this.state.visiable} onHide={() => this.hide()}>
        <Modal.Header closeButton>
          <Modal.Title>{this.props.title}</Modal.Title>
        </Modal.Header>
        <Modal.Body>{this.props.children}</Modal.Body>
        {this.footer ? (
          <Modal.Footer>
            <Button variant="secondary" onClick={() => this.hide()}>
              إغلاق
            </Button>
          </Modal.Footer>
        ) : (
          <></>
        )}
      </Modal>
    );
  }
}

export { DialogBox, mDialog };
