class GenericBuilding {
    name = "";
    description = "";
    villageId = 0;
    buildingId = 0;
    placeId = 0;
    level = 0;
    /** @type {Number[][]} */
    costs = [];
    /** @type {Number[]} */
    culturePoints = [];
    /** @type {Number[]} */
    consumption = [];
    /** @type {Number[][]} */
    preRequiresites = [];
    /** @type {Number[]} */
    time = [];
    effect = [];
    capital = 0;
    race = [true, true, true, true, true];

    get title() {
        return `${this.name} المستوى ${this.level}`;
    }
    /** @returns {String} */
    get image() {
        return ImageMap[this.buildingId] || "";
    }
    get maxLevel() {
        return this.consumption.length;
    }
    /** @returns {String} */
    get PolygonPoints() {
        return Village.Polygon[this.placeId];
    }
    /** @returns {{x: Number, y: Number}} */
    get Position() {
        return Village.Position[this.placeId];
    }
    get ImageComponent() {
        let P = this.Position;
        return <image
            key={`Image-B${this.buildingId}-P${this.placeId}`}
            xlinkHref={this.image}
            x={P.x}
            y={P.y}
            width={this.buildingId === 16 ? "69" : "75"}
            height={this.buildingId === 16 ? "120" : "100"}
        />;
    }
    get LinkComponent() {
        return <Link key={`Link-B${this.buildingId}-P${this.placeId}`} to={"/Building?id=" + this.placeId}>
            <OverlayTrigger placement="top" overlay={<Tooltip id={`Center-Tooltip-B${this.buildingId}-P${this.placeId}`}>{this.title}</Tooltip>}>
                <polygon points={this.PolygonPoints} opacity={0.1} />
            </OverlayTrigger>
        </Link>;
    }
}


