const Building = {
    /** @returns {GenericBuilding} */
    New(BuildingId = 1, VillageId = 0, PlaceId = 0, Level = 0) {
        return new (BuildingsMap[BuildingId] || BuildingsMap[1])(VillageId, PlaceId, Level);
    },
    /** @returns {String} */
    Name(BuildingId = 1) {
        return BuildingsName[BuildingId] || "";
    },
    /** @returns {Number[]} */
    Type(Type = 1) {
        return BuildingsType[Type] || [1];
    },
    /** @param {Number[]} B * @param {Number} vId */
    FromResponse(B, vId) {
        return Building.New(B[1], vId, B[0], B[2]);
    }
}

export default Building;

