const fn = {
    toSeconds(time = "") { // converts "01:30:25" to "5425 seconds"
        let t = time.split(":");
        return (t[0] * 3600 + t[1] * 60 + parseInt(t[2]));
    },
    toTime(seconds = 0) { // converts "5425 seconds" to "01:30:25"
        let hh = Math.floor(seconds / 3600);
        let mm = Math.floor(seconds / 60) % 60;
        let ss = Math.floor(seconds) % 60;
        if (hh < 10) hh = "0" + hh;
        if (mm < 10) mm = "0" + mm;
        if (ss < 10) ss = "0" + ss;
        return (hh + ":" + mm + ":" + ss);
    }
}

export default fn;